import { getImagesByQuery } from './js/pixabay-api.js';
import {
  createGallery,
  clearGallery,
  showLoader,
  hideLoader,
  showLoadMoreButton,
  hideLoadMoreButton,
} from './js/render-functions.js';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const searchForm = document.querySelector('.form');
const loadMoreBtn = document.querySelector('.load-more');
let currentPage = 1;
let currentQuery = '';
const perPage = 15;
let totalHits = 0;

searchForm.addEventListener('submit', handleSearchSubmit);

if (loadMoreBtn) {
  loadMoreBtn.addEventListener('click', handleLoadMore);
}

async function handleSearchSubmit(event) {
  event.preventDefault();

  const searchInput = event.target.elements.searchQuery;
  const searchQuery = searchInput.value.trim();

  if (!searchQuery) {
    iziToast.warning({
      title: 'Warning',
      message: 'Please enter a search term!',
      position: 'topRight',
    });
    return;
  }

  clearGallery();
  currentPage = 1;
  currentQuery = searchQuery;
  hideLoadMoreButton();

  showLoader();
  
  try {
    const data = await getImagesByQuery(searchQuery, currentPage);
    await handleSearchResponse(data);
  } catch (error) {
    handleError(error);
  } finally {
    hideLoader();
  }
}

async function handleLoadMore() {
  try {
    currentPage++;
    showLoader();
    hideLoadMoreButton();

    const data = await getImagesByQuery(currentQuery, currentPage);
    const previousImageCount = document.querySelectorAll('.gallery-item').length;
    
    createGallery(data.hits);
    
    smoothScroll(previousImageCount);
    if (data.totalHits) {
      totalHits = data.totalHits;
    }
    
    const maxPage = Math.ceil(totalHits / perPage);

    if (currentPage < maxPage) {
      showLoadMoreButton();
    } else {
      hideLoadMoreButton();
      iziToast.info({
        title: 'Info',
        message: "We're sorry, but you've reached the end of search results.",
        position: 'topRight',
      });
    }
  } catch (error) {
    console.error("Error loading more images:", error);
    currentPage--;
    
    if (currentPage >= 1) {
      showLoadMoreButton();
    }
    
    iziToast.error({
      title: 'Error',
      message: 'Failed to load more images. Please try again.',
      position: 'topRight',
    });
  } finally {
    hideLoader();
  }
}

function handleSearchResponse(data) {
  const images = data.hits;
  totalHits = data.totalHits;
  const totalPages = Math.ceil(totalHits / perPage);

  if (images.length === 0) {
    iziToast.error({
      title: 'Error',
      message:
        'Sorry, there are no images matching your search query. Please try again!',
      position: 'topRight',
    });
    return;
  }

  createGallery(images);

  if (currentPage < totalPages) {
    showLoadMoreButton();
  } else {
    hideLoadMoreButton();
    if (images.length > 0) {
      iziToast.info({
        title: 'Info',
        message: "All search results have been loaded.",
        position: 'topRight',
      });
    }
  }

  iziToast.success({
    title: 'Success',
    message: `Hooray! We found ${totalHits} images.`,
    position: 'topRight',
  });
}

function handleError(error) {
  console.error('Error:', error);
  
  iziToast.error({
    title: 'Error',
    message: 'Something went wrong. Please try again later!',
    position: 'topRight',
  });
}


function smoothScroll(previousCount) {
  const galleryItems = document.querySelectorAll('.gallery-item');
  if (galleryItems.length <= previousCount) return;
  
  const newItems = Array.from(galleryItems).slice(previousCount);
  if (newItems.length === 0) return;
  
  const firstNewItem = newItems[0];
  const cardHeight = firstNewItem.getBoundingClientRect().height;
  
  window.scrollBy({
    top: cardHeight * 2,
    behavior: 'smooth',
  });
}